<template>
  <div class="main-wrapper">
    <div class="header"><h1>Vue Shopping Cart</h1></div>
    <div id="vue">
      <cart :data="data"></cart>
      <products :data="data"></products>
      <checkout-area v-if="data.checkoutBool" :data="data"></checkout-area>
    </div>
  </div>
</template>

<script>
  import xlsx from 'xlsx'
  import Cart from './components/Cart'
  import Products from './components/Products'
  import CheckoutArea from './components/CheckoutArea'
  import path from 'path'

  export default {
    name: 'App',
    components: {Cart, Products, CheckoutArea},
    data() {
      return {
        data: {
          productsData: [],
          checkoutBool: false,
          cart: [],
          cartSubTotal: 0,
          tax: 0.065,
          cartTotal: 0
        }

        // //intercept the checkout request dispatch
        // //send it back down the chain
        // events: {
        // "checkoutRequest": function() {
        //   vue.$broadcast("checkoutRequest");
        // }
        // }
      }
    },

    methods: {
      getProductsFromExcel: function f(products) {

        var request = new XMLHttpRequest();
        request.open('GET', 'static/Prod.xls', true);
        request.responseType = 'blob';
        request.onload = function() {
          var reader = new FileReader();
          console.log(request.response)
          reader.readAsBinaryString(request.response);
          reader.onload =  function(e){
            handleExcel(e.target.result);
          };
        };
        request.send();


        function handleExcel(excelFileContent) {


          let parsedExcel = xlsx.read(excelFileContent, {type: "binary"});
          let excelSheet = parsedExcel.Sheets[parsedExcel.SheetNames[0]];
          let rows = xlsx.utils.sheet_to_json(excelSheet, {header: true});

          console.log(rows)

          products.push(...rows.map((p, i) => {
            return {
              sku: i,
              image: "static/images/" + p.product + ".jpg",
              product: p.product,
              price: parseInt(p.price),
              description: p.name,
              details: p.name
            }
          }))

          console.log(products)
      };
      }
    },
    mounted() {
      this.getProductsFromExcel(this.data.productsData)
    }
  }
</script>


<style lang="scss">
  @import url(https://fonts.googleapis.com/css?family=Bitter:400,400italic,700);
  @import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css);

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Bitter";
  }

  $mobile: 600px;
  $max-width: 80em;

  @mixin aspect-ratio($width, $height) {
    position: relative;
    &:before {
      display: block;
      content: "";
      width: 100%;
      padding-top: ($height / $width) * 100%;
    }
    > .content {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }

  @keyframes openUp {
    0% {
      transform: translate(-50%, -50%) scale(0.5);
    }

    100% {
      transform: translate(-50%, -50%) scale(1);
    }
  }

  button {
    appearance: none;
    padding: 0.5em;
    margin: 0.5em 0;
    background: white;
    border: 1px solid black;
    transition: all 0.1s;
    font-size: 14px;
    cursor: pointer;
    user-select: none;

    .fa {
      font-size: 1em !important;
      vertical-align: middle;
    }

    &:hover {
      color: white;
      background: black;
    }
  }

  input {
    display: inline-block;
    appearance: none;
    padding: 0.5em;
    margin: 0.5em 0.5em 0.5em 0;
    width: 50px;
    background: white;
    border: 1px solid black;
    transition: all 0.1s;
    font-size: 14px;
  }

  label {
    font-size: 0.75em;
    margin-right: 0.5em;
  }

  .checkout-area table {
    margin: 0 auto;
    padding: 0.5em;
    width: 100%;
    max-width: $max-width * 0.5;
    text-align: left;

    th, td {
      padding: 0 0.25em;
    }

    @media(max-width: $mobile) {
      th:nth-child(3), td:nth-child(3) {
        display: none;
      }
    }
  }

  .align-left {
    text-align: left;
  }

  .align-center {
    text-align: center;
  }

  .align-right {
    text-align: right;
  }

  .vert-bottom {
    vertical-align: bottom;
  }

  .vert-middle {
    vertical-align: middle;
  }

  .main-wrapper {
    .header {
      position: relative;
      background: linear-gradient(to left, #16222A, #3A6073);
      background-size: cover;
      height: 25em;
      width: 100vw;
      box-shadow: inset -1px -3px 5px rgba(0, 0, 0, 0.5), inset 1px 3px 5px rgba(0, 0, 0, 0.5);

      h1 {
        position: absolute;
        text-align: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: white;
        font-size: 3em;
        text-shadow: 1px 3px 5px rgba(0, 0, 0, 0.5), -1px -3px 5px rgba(0, 0, 0, 0.5);
      }
    }

    #vue {
      margin: 0 auto;
      padding: 0.5em;
      text-align: center;

      .cart {
        position: fixed;
        right: 0em;
        top: 0em;
        text-align: right;
        background: rgba(0, 0, 0, 0.85);
        color: white;
        z-index: 1;

        .fa-shopping-cart, .cart-size {
          cursor: pointer;
          font-size: 1.25em;
          user-select: none;
        }

        .fa-shopping-cart {
          padding: 1em 1em 1em 0;
        }

        .cart-size {
          padding: 1em 0 1em 1em;
        }

        .cart-items {
          padding: 0 1em 2em 1em;

          .cartTable {
            width: 20em;
          }

          .cartImage {
            width: 4em;
            height: 4em;
            margin: 0.5em auto;
            position: relative;
            cursor: pointer;

            &:after {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: rgba(0, 0, 0, 0.25);
              transition: all 0.1s;
            }

            i {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              font-size: 1.5em;
              z-index: 1;
              transition: all 0.25s;
            }

            &:hover {
              i {
                text-shadow: 1px 2px 5px black;
              }

              &:after {
                background: rgba(0, 0, 0, 0.5);
              }
            }

            &:active {
              i {
                text-shadow: 0px 0px 1px black;
              }
            }
          }

          .cartSubTotal {
            border-top: 1px solid white;
            font-size: 1.25em;
          }

          .clearCart {
            float: left;
            margin-top: 2em;
            margin-bottom: 1.25em;
          }

          .checkoutCart {
            float: right;
            margin-top: 2em;
            margin-bottom: 1.25em;
          }
        }
      }

      .products {
        margin: 0 auto;
        width: 100%;
        max-width: $max-width;

        .product {
          display: inline-block;
          margin: 0.75em;
          width: 100%;
          max-width: 18em;

          .image {
            width: 18em;
            height: 18em;
            margin-bottom: 0.5em;
            position: relative;
            overflow: hidden;
            cursor: pointer;
            transition: box-shadow 0.25s;

            @media(max-width: $mobile) {
              width: 100vw;
              height: 100vw;
            }

            &:before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: rgba(0, 0, 0, 0.5);
              opacity: 0;
              transition: all 0.25s;
            }

            &:after {
              content: "\f00e";
              font-family: "FontAwesome";
              position: absolute;
              top: 250%;
              left: 50%;
              transform: translate(-50%, -50%);
              font-size: 5em;
              color: white;
              pointer-events: none;
              text-shadow: 0 0 5px rgba(0, 0, 0, 0.5), 0 0 10px rgba(0, 0, 0, 0.5);
              transition: all 0.25s;
            }

            &:hover {
              box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);

              &:before {
                opacity: 1;
              }

              &:after {
                top: 50%;
              }
            }
          }

          .name {
            font-weight: bold;
            font-size: 1.25em;
          }

          .description {
            font-style: italic;
          }

          .price {
            font-weight: bold;
          }
        }
      }

      .modalWrapper {
        position: relative;

        .overlay {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.7);
          //cursor: pointer;
          z-index: 2;
        }

        .prevProduct, .nextProduct {
          position: fixed;
          color: white;
          font-size: 2em;
          cursor: pointer;
          top: 50%;
          transform: translateY(-50%);
          padding: 1em;
          user-select: none;
          z-index: 5;
        }

        .prevProduct {
          left: calc(50% - 9.5em);
        }

        .nextProduct {
          right: calc(50% - 9.5em);
        }

        .modal {
          position: fixed;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background: white;
          padding: 2em;
          text-align: center;
          max-height: calc(100% - 2em);
          overflow-y: scroll;
          overflow-x: hidden;
          animation: openUp 0.1s;
          z-index: 3;
          backface-visibility: hidden;

          .close {
            position: fixed;
            top: -0.5em;
            right: -0.5em;
            cursor: pointer;
            padding: 1em;
          }

          .imageWrapper {
            width: 25em;
            height: 25em;
            margin: 0.5em auto;
            overflow: hidden;

            .image {
              width: 100%;
              height: 100%;
              transition: transform 0.2s;
              z-index: 4;
              cursor: crosshair;
            }
          }

          .additionalImages {
            display: flex;
            width: 100%;
            margin: 0 auto;
            text-align: center;

            .additionalImage {
              flex-grow: 1;
              margin: 0.5em 1em;
              position: relative;
              overflow: hidden;
              cursor: pointer;
              width: 25%;
              height: auto;
              @include aspect-ratio(16, 12);

              &:nth-of-type(1n) {
                margin-left: 0;
              }

              &:nth-of-type(4n) {
                margin-right: 0;
              }
            }
          }

          .name {
            font-weight: bold;
            font-size: 1.25em;
          }

          .description {
            font-style: italic;
          }

          .details {
            max-width: 25em;
            margin: 0 auto;
            padding: 0.5em 0;
          }

          .price {
            font-weight: bold;
            padding-bottom: 0.5em;
          }
        }
      }

      @media(max-width: $mobile) {
        .cart {
          &, & .cartTable {
            width: 100% !important;
          }
        }

        .products {
          text-align: left;

          .product {
            display: block;

            .image {
              width: calc(100vw - 2.5em);
              height: calc(100vw - 2.5em);
            }
          }
        }

        .modalWrapper {
          .prevProduct, .nextProduct {
            display: none;
          }

          .modal {
            &.checkout {
              width: 100%;
            }

            .imageWrapper {
              width: calc(100vw - 4em);
              height: calc(100vw - 4em);
            }
          }
        }
      }
    }
  }
</style>



